import * as React from "react";
import * as ReactDOM from "react-dom";
import { RouteComponentProps } from 'react-router';
import { Button, Form, FormGroup, Label, Input, FormFeedback, FormText } from 'reactstrap';

export class EdytujProfilUzytkownika extends React.Component<RouteComponentProps<{}>, {}> {
    public render() {
        return <div>

            <Form>
                <FormGroup>
                    <Label for="email">Email</Label>
                    <Input />
                    {/*<FormFeedback>Email jest dostpny</FormFeedback>*/}
                    <FormText>Wprowad email, ktry bdzie suy jako nazwa uytkownika</FormText>
                </FormGroup>
                <FormGroup>
                    <Label for="haslo">Haso</Label>
                    <Input />
                    {/*<FormFeedback valid>Sweet! that name is available</FormFeedback>*/}
                    <FormText>Wprowad haso do konta</FormText>
                </FormGroup>
                <FormGroup>
                    <Label for="powtorzHaslo">Powtrz haso</Label>
                    <Input />
                    {/*<FormFeedback>Oh noes! that name is already taken</FormFeedback>*/}
                    <FormText>Potwrz haso</FormText>
                </FormGroup>
                <FormGroup>
                    <Label for="imie">Imi</Label>
                    <Input /> {/* <Input valid/> <Input invalid /> */}
                    {/*<FormFeedback valid>Sweet! that name is available</FormFeedback>*/}
                    <FormText>Podaj swoje imi</FormText>
                </FormGroup>
                <FormGroup>
                    <Label for="nazwisko">Nazwisko</Label>
                    <Input />
                    {/*<FormFeedback>Oh noes! that name is already taken</FormFeedback>*/}
                    <FormText>Podaj swoje nazwisko</FormText>
                </FormGroup>
                <FormGroup>
                    <Label for="nrTelefonu">Numer telefonu</Label>
                    <Input />
                    {/*<FormFeedback>Oh noes! that name is already taken</FormFeedback>*/}
                    <FormText>Podaj swj numer telefonu</FormText>
                </FormGroup>
                <Button>Zarejestruj si</Button>
            </Form>

        </div>;
    }
}



